'use client'

import { useState } from 'react'
import { format, addDays, addWeeks, addMonths, addYears } from 'date-fns'
import { nb } from 'date-fns/locale'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Calendar } from "@/components/ui/calendar"
import { MaintenanceTask, RepeatInterval } from '@/types/maintenance'
import { NewTaskDialog } from '@/components/planlegging/NewTaskDialog'
import { TaskList } from '@/components/planlegging/TaskList'
import { TaskDetails } from '@/components/planlegging/TaskDetails'

const mockTasks: MaintenanceTask[] = [
  {
    id: '1',
    title: 'Ã…rlig takkontroll',
    description: 'Grundig inspeksjon av tak og takrenner',
    customerId: 'cust1',
    customerName: 'Ola Nordmann',
    address: 'Bassengvegen 18',
    startDate: new Date(2024, 3, 15, 10, 0),
    endDate: new Date(2024, 3, 15, 12, 0),
    type: 'vedlikehold',
    status: 'planlagt',
    repeatInterval: 'yearly',
    notifications: {
      customer: true,
      worker: true,
      reminderDays: 7
    }
  },
  // ... flere eksempeloppgaver
]

export default function Planlegging() {
  const [tasks, setTasks] = useState<MaintenanceTask[]>(mockTasks)
  const [selectedDate, setSelectedDate] = useState<Date>(new Date())
  const [selectedTask, setSelectedTask] = useState<MaintenanceTask | null>(null)
  const [isNewTaskDialogOpen, setIsNewTaskDialogOpen] = useState(false)

  const handleAddTask = (task: MaintenanceTask) => {
    const newTasks = [task]
    
    // Opprett repeterende oppgaver
    if (task.repeatInterval !== 'never' && task.repeatUntil) {
      let currentDate = task.startDate
      let currentEndDate = task.endDate
      
      while (currentDate < task.repeatUntil) {
        let nextDate: Date
        let nextEndDate: Date
        
        switch (task.repeatInterval) {
          case 'daily':
            nextDate = addDays(currentDate, 1)
            nextEndDate = addDays(currentEndDate, 1)
            break
          case 'weekly':
            nextDate = addWeeks(currentDate, 1)
            nextEndDate = addWeeks(currentEndDate, 1)
            break
          case 'monthly':
            nextDate = addMonths(currentDate, 1)
            nextEndDate = addMonths(currentEndDate, 1)
            break
          case 'yearly':
            nextDate = addYears(currentDate, 1)
            nextEndDate = addYears(currentEndDate, 1)
            break
        }
        
        if (nextDate > task.repeatUntil) break
        
        newTasks.push({
          ...task,
          id: `${task.id}-${format(nextDate, 'yyyy-MM-dd')}`,
          startDate: nextDate,
          endDate: nextEndDate
        })
        
        currentDate = nextDate
        currentEndDate = nextEndDate
      }
    }
    
    setTasks(prev => [...prev, ...newTasks])
    setIsNewTaskDialogOpen(false)
  }

  const handleUpdateTaskStatus = (taskId: string, status: MaintenanceTask['status']) => {
    setTasks(prev => prev.map(task => 
      task.id === taskId ? { ...task, status } : task
    ))
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Planlegging</h1>
        <Button 
          className="bg-green-600 hover:bg-green-700"
          onClick={() => setIsNewTaskDialogOpen(true)}
        >
          + Ny oppgave
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-12 gap-6">
        <div className="lg:col-span-8">
          <Card className="p-4">
            <Calendar
              mode="single"
              selected={selectedDate}
              onSelect={setSelectedDate}
              locale={nb}
              modifiers={{
                hasTask: (date) => tasks.some(task => 
                  task.startDate.toDateString() === date.toDateString()
                )
              }}
              modifiersStyles={{
                hasTask: {
                  fontWeight: 'bold',
                  backgroundColor: '#f3f4f6'
                }
              }}
            />
          </Card>
        </div>

        <div className="lg:col-span-4 space-y-4">
          <TaskList
            tasks={tasks}
            selectedDate={selectedDate}
            onSelectTask={setSelectedTask}
          />
          
          {selectedTask && (
            <TaskDetails
              task={selectedTask}
              onUpdateStatus={handleUpdateTaskStatus}
            />
          )}
        </div>
      </div>

      <NewTaskDialog
        open={isNewTaskDialogOpen}
        onOpenChange={setIsNewTaskDialogOpen}
        onAddTask={handleAddTask}
      />
    </div>
  )
} 